*::-webkit-scrollbar {
  display: none;
}

.tag {
  display: inline-block;
  padding: 5px 10px;
  margin-right: 5px;
  font-size: 14px;
  line-height: 14px;
  background-color: #3dd5f3;
  border-radius: 30px;
}

.input-clear {
  position: absolute;
  width: 20px;
  right: 63px;
  top: 50%;
  transform: translateY(-50%);
  z-index: 5;
}
